<template>
  <div class="cell" @click="opennative(userId, nickName)">
    <div class="cell-left">
      <slot name="image">
      </slot>
      <em class="level-pic">
        <img src="../assets/ic_focus.png" @click.stop="foucedClick(1, index, userId, hasFocus)">
      </em>

    </div>
    <div class="cell-center">
      <div class="name">
        <em><slot name="nickname"></slot></em>
      </div>

      <div class="title">
        <em><slot name="title"></slot></em>
      </div>
      <div class="percent"><em><slot name="goodSclass"></slot>:</em><em><slot name="playWinRate"></slot>%</em><em><slot name="goodPlay"></slot>:</em><em><slot name="sclassWinRate"></slot>%</em>
      </div>
    </div>
    <div class="cell-right"><slot name="count"></slot></div>
  </div>
</template>

<script>

  export default {
    props: {
      userId: Number,
      nickName: String,
      index: Number,
      hasFocus: Boolean
    },
    data: function() {
      return {
        image: "",
        token: ""
      }
    },
    methods: {
      opennative: function(val, name) {
        var datas = {
          n: "userinfo",
          v: {
            uid: val,
            nickname: name
          }
        }
        gq.open(datas);
      },
      foucedClick: function(type, key, val, hasfouce) {
        this.token = $vm0.$root.$children[0].token
        if (this.token == "") {
          gq.toLogin()
        } else {
          if (hasfouce) {
            this.focusRemove(type, key, val);
          } else {
            this.focusAdd(type, key, val)
          }
        }
      },
      focusAdd: function(type, key, leaderId) {
        this.$http.post(url + "focus/add?leaderId=" + leaderId + "&followerId=" + this.userId).then(function(response) {
          var res = response.data;
          if (res.code == "200") {
            gq.toast("关注成功");
            if (type == 1) {
              this.saleList[key].hasFocus = true
            } else {
              this.redList[key].hasFocus = true
            }
          } else {
            gq.toast(res.msg);
          }
        }, function(response) {
          gq.toast(response.data.msg);
        })
      },
      focusRemove: function(type, key, leaderId) {
        this.$http.post(url + "focus/remove?leaderId=" + leaderId + "&followerId=" + this.userId).then(function(response) {
          var res = response.data;
          if (res.code == "200") {
            gq.toast("取消成功");
            if (type == 1) {
              this.saleList[key].hasFocus = false
            } else {
              this.redList[key].hasFocus = false
            }
          } else {
            gq.toast(res.msg);
          }
        }, function(response) {
          gq.toast(response.data.msg);
        })
      }
    }
  }
  
</script>