<template>
  <!-- <div id="app">
    <Nav/>
    <Content/>
  </div> -->

  <div id="app">
    <Nav v-on:hot-slae="currentTab=0" v-on:hot-expert="currentTab=1"/>
    <keep-alive>
      <component v-bind:is="currentTabComponent" class="tab"></component>
    </keep-alive>
    
  </div>
    
</template>

<script>
import Nav from './components/Nav.vue'
import Content0 from './components/Content.vue'
import Content1 from './components/Content1.vue'

export default {
  name: 'app',
  created: function() {
    gq.ready(function() {
    //获取token 
      var token = "",
      userId = ""
      gq.getToken(function(res) {
        if (res.token != null) {
          token = res.token
          gq.info(function(res) {
            
            $root.$children[0].token = res.token
            $root.$children[0].userId = res.userId
          })
        }
      })
      gq.addEventListener("reload", function() {
        gq.getToken(function(res) {
          if (res.token != null) {
            vum.token = res.token
            gq.info(function(res) {
              vum.userId = res.userId;
            })
          } else {
            vum.token = ""
          }
          Vue.http.headers.common['token'] = vum.token
          vum.limitStart = 0
          vum.limitStart1 = 0
        });
      })
    })

  },
  data: function() {
    return {
      mescroll: '',
      currentTab: 0,
      token: "",
      userId: "",
      platform: ""
    }
  },
  computed: {
    currentTabComponent: function () {
      return 'Content' + this.currentTab
    }
  },
  components: {
    Nav,
    Content0,
    Content1
  },
  
}
</script>

<style>
/**{user-select:none;}*/
.mescroll{
    position: fixed;
    top: 64px;
    bottom: 0;
    height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
