<style scoped>
.content {
  display: flex;
  width: 100%;
  background-color: #F3F4F6;
  flex-direction: row;
  align-content:flex-start;
  flex-wrap:wrap;
}

.cell {
  margin-bottom: 10px;
}
</style>

<template>
  <div id="mescroll" class="mescroll">
    <div>
      <div class="content">
        <template v-for="(v,key) in saleList">
          <Cell :nickName="v.nickName" :userId="v.userId" :index="key" :hasFocus="v.hasFocus">
            <template v-slot:nickname>
              {{v.nickName}}
            </template>

            <template v-slot:title>
              {{v.levelName}}
            </template>

            <template v-slot:count>
              {{v.count}}
            </template>

            <template v-slot:goodSclass>
              {{v.goodSclass}}
            </template>

            <template v-slot:goodPlay>
              {{v.goodPlay==1?"胜平负": (v.goodPlay==2?"让球":"进球数")}}
            </template>

            <template v-slot:playWinRate>
              {{v.playWinRate}}
            </template>

            <template v-slot:sclassWinRate>
              {{v.sclassWinRate}}
            </template>

            <template v-slot:image>
              <img class="avatar" :src="'https://mobile.gunqiu.com/avatar/'+v.userId+'?size=!100px'" alt="">
              
            </template>
          </Cell>
        </template>
      </div>
    </div>
  </div>
  
</template>

<script>
import Cell from './Cell.vue'
export default {
  name: 'Content',
  components: {
    Cell
  },
  data: function() {
      return {
        mescroll: "",
        saleList: [],
        entry: "",
        more1: true,
        limitStart: 0
      }
  },
  created: function() {
    this.$http.get('https://mobile.gunqiu.com/interface/v3.6/rec/hotsalers/list?limitStart=0&limitNum=20')
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == "200") {
            this.saleList = response.data.data;
          }
          console.log("success");
        } else {
          console.log("fail");
        }
    })
  },
  mounted: function() {
    var that = this;
    var url = "https://mobile.gunqiu.com/interface/v3.6/";
    this.mescroll = new MeScroll("mescroll", {
      down: {
          auto: false,
          callback: function() {
              that.limitStart = 0;
              that.$http.get(url + 'rec/hotsalers/list?limitStart=' + that.limitStart + '&limitNum=20').then(function(response) {
                  var res = response.data;
                  if (res.code == "200") {
                      that.more1 = true
                      if (res.data.length > 0) {
                          if (res.data.length < 20) {
                              that.more1 = false
                          }
                      } else {
                          that.more1 = false
                      }
                      that.mescroll.endSuccess();
                      that.saleList = res.data
                  } else {
                      that.mescroll.endErr();
                      that.more1 = false
                  }
              }, function(response) {
                  that.mescroll.endErr();
                  that.more1 = false
              })
          }
      },
      up: {
          auto: false,
          // isBounce: false,
          callback: function() {
            console.log("pull")
              if (that.more1) {
                  that.limitStart = that.limitStart + 20
                  that.$http.get(url + 'rec/hotsalers/list?limitStart=' + that.limitStart + '&limitNum=20').then(function(response) {
                      var res = response.data;
                      if (res.code == "200") {
                          if (res.data.length < 20) {
                              that.more1 = false
                          } else {
                              that.more1 = true
                          }
                          that.mescroll.endBySize(that.saleList.length);
                          that.saleList = that.saleList.concat(res.data);
                      } else {
                          that.mescroll.endErr();
                      }
                  }, function(response) {
                      that.mescroll.endErr();
                  })
              } else {
                  that.mescroll.endErr();
              }
          },
          // toTop: {
          //     //配置回到顶部按钮
          //     src: "mescroll/mescroll-totop.png",
          // },
          htmlNodata: '<p class="upwarp-nodata">———  中奖更简单  ———</p>'
      }
  });
    // this.mescroll = new MeScroll("mescroll", {
    //   down: {
    //     callback: function() {
    //       that.mescroll.endSuccess();
    //     }
    //   },
    //   up: {
    //     callback: function() {
    //       that.mescroll.endSuccess();
    //     }
    //   }
        
    // });
  }
}
</script>