<style  scoped>
.content {
  display: flex;
  width: 100%;
  background-color: #F3F4F6;
  flex-direction: row;
  align-content:flex-start;
  flex-wrap:wrap;
}

.cell {
  margin-bottom: 10px;
}
</style>

<template>
  <div id="mescroll" class="mescroll">
    <div>
      <div class="content">
        <template v-for="(v,key) in saleList">
          <Cell :nickName="v.nickName" :userId="v.userid" :index="key" :hasFocus="v.hasFocus">
            <template v-slot:nickname>
              {{v.nickName}}
            </template>

            <template v-slot:title>
              {{v.levelName}}
            </template>

            <template v-slot:count>
              {{v.count}}
            </template>

            <template v-slot:goodSclass>
              {{v.goodSclass}}
            </template>

            <template v-slot:goodPlay>
              {{v.goodPlay==1?"胜平负": (v.goodPlay==2?"让球":"进球数")}}
            </template>

            <template v-slot:playWinRate>
              {{v.playWinRate}}
            </template>

            <template v-slot:sclassWinRate>
              {{v.sclassWinRate}}
            </template>

            <template v-slot:image>
              <img class="avatar" :src="v.avatar" alt="">
            </template>
          </Cell>
        </template>
      </div>
    </div>
  </div>
  
</template>

<script>
import Cell from './Cell.vue'
export default {
  name: 'Content',
  components: {
    Cell
  },
  data: function() {
      return {
        mescroll: '',
        saleList: [],
        entry: "",
        abc: "sfklj",
        more2: true,
        limitStart1: 0
      }
  },
  created: function() {
    this.$http.get('https://mobile.gunqiu.com/interface/v3.6/hotexpert/list?limitStart=0&limitNum=20')
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == "200") {
            this.saleList = response.data.data;
          }
          console.log("success");
        } else {
          console.log("fail");
        }
    })
  },
  mounted: function() {
    var that = this;

    var url = "https://mobile.gunqiu.com/interface/v3.6/";
    this.mescroll = new MeScroll("mescroll", {
        down: {
            auto: false,
            callback: function() {
                that.limitStart1 = 0
                that.$http.get(url + 'hotexpert/list?limitStart=' + that.limitStart1 + '&limitNum=20').then(function(response) {
                    var res = response.data;
                    if (res.code == "200") {
                        that.more2 = true
                        if (res.data.length > 0) {
                            if (res.data.length < 20) {
                                that.more2 = false
                            }
                        } else {
                            that.more2 = false
                        }
                        that.mescroll.endSuccess();
                        that.saleList = res.data
                    } else {
                        that.mescroll.endErr();
                        that.more2 = false
                    }
                }, function(response) {
                    that.mescroll.endErr();
                    that.more2 = false
                })
            }
        },
        up: {
            auto: false,
            // isBounce: false,
            callback: function() {

                if (that.more2) {
                  
                    that.limitStart1 = that.limitStart1 + 20
                    that.$http.get(url + 'hotexpert/list?limitStart=' + that.limitStart1 + '&limitNum=20').then(function(response) {
                      console.log("pull")
                        var res = response.data;
                        if (res.code == "200") {
                            if (res.data.length < 20) {
                                that.more2 = false
                                console.log("all loading...")
                            } else {
                                that.more2 = true
                            }
                            that.mescroll.endBySize(that.saleList.length);
                            that.saleList = that.saleList.concat(res.data);
                        } else {
                            that.mescroll.endErr();
                        }
                    }, function(response) {
                        that.mescroll.endErr();
                    })
                } else {
                    that.mescroll.endErr();
                }
            },
            toTop: {
                //配置回到顶部按钮
                // src: "mescroll/mescroll-totop.png",
            },
            htmlNodata: '<p class="upwarp-nodata">———  中奖更简单  ———</p>'
        }
    });
    // this.mescroll = new MeScroll("mescroll", {
    //   down: {
    //     callback: function() {
    //       that.mescroll.endSuccess();
    //     }
    //   },
    //   up: {
    //     callback: function() {
    //       that.mescroll.endSuccess();
    //     }
    //   }
        
    // });
  }
}
</script>